/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  display: flex;
  width: 100%;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

/** Media Queries **/
.contenido {
  display: flex;
  align-items: center;
  flex-direction: column;
}
.contenido__h3 {
  font-weight: 700;
}
.contenido__h4 {
  font-weight: 300;
}

.anuncio {
  padding-bottom: 2.5rem;
  max-width: 80rem;
}

/** Media Queries **/
.boton-verde {
  background-color: #71B100;
  border: none;
  display: flex;
  width: fit-content;
  padding: 1rem 2rem;
  font-weight: 700;
  margin-bottom: 2rem;
  color: #FFFFFF;
}
.boton-verde:hover {
  background-color: #619800;
  cursor: pointer;
}

.boton-amarillo {
  background-color: #E08709;
  border: none;
  display: flex;
  width: fit-content;
  padding: 1rem 2rem;
  font-weight: 700;
  margin-bottom: 2rem;
  color: #FFFFFF;
}
.boton-amarillo:hover {
  background-color: #c77808;
  cursor: pointer;
}

.boton-rojo {
  background-color: rgb(202, 13, 13);
  border: none;
  display: flex;
  width: fit-content;
  padding: 1rem 2rem;
  font-weight: 700;
  margin-bottom: 2rem;
  color: #FFFFFF;
}
.boton-rojo:hover {
  background-color: #b20b0b;
  cursor: pointer;
}

.botones {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: fit-content;
}

.contactanos {
  position: relative;
}
.contactanos picture img {
  min-height: 50rem;
  object-fit: cover;
}
.contactanos__banner {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: absolute;
  width: 100%;
  height: 100%;
  padding: 2rem;
  top: 0;
}
.contactanos__banner h2, .contactanos__banner p {
  color: #FFFFFF;
}

.contacto__forma {
  display: flex;
  flex-direction: column;
  width: 100%;
}

/** Media Queries **/
.blog-testimoniales {
  max-width: 120rem;
  padding: 0 2.5rem;
}
@media (min-width: 1024px) {
  .blog-testimoniales {
    display: grid;
    grid-template-columns: 2fr 1fr;
  }
}

.testimoniales {
  padding: 1rem;
  background-color: #71B100;
  border-radius: 2rem;
  color: #FFFFFF;
}
.testimoniales blockquote::before {
  content: "";
  background-image: url(../img/comilla.svg);
  background-repeat: no-repeat;
  width: 6rem;
  height: 8rem;
  display: block;
  position: absolute;
  left: -5.5rem;
}
.testimoniales blockquote {
  position: relative;
  margin-left: 5.5rem;
  font-size: 2rem;
}
.testimoniales p {
  text-align: end;
  color: #FFFFFF;
  font-weight: 700;
}

.darkmode {
  background-color: #1f1f1f;
}
.darkmode h1, .darkmode h2, .darkmode h3, .darkmode h4, .darkmode p, .darkmode label {
  color: #FFFFFF;
}
.darkmode input, .darkmode textarea, .darkmode select, .darkmode option {
  border: none;
  background-color: #404040;
  color: #FFFFFF;
}
.darkmode .anuncios__card {
  background-color: #404040;
}
.darkmode .icono {
  filter: invert(100%);
}

.propiedades {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

table {
  width: 100%;
  max-width: 120rem;
  text-align: center;
}
table thead {
  background-color: #71B100;
  color: #FFFFFF;
}
table img {
  width: 20rem;
  height: 10rem;
}

.alerta-verde {
  width: 100%;
  background-color: #71B100;
  max-width: 120rem;
  margin-bottom: 2rem;
  text-align: center;
  color: #FFFFFF;
  text-transform: uppercase;
  font-weight: 700;
  padding: 1rem;
}

.alerta-roja {
  width: 100%;
  background-color: rgb(202, 13, 13);
  max-width: 120rem;
  margin-bottom: 2rem;
  text-align: center;
  color: #FFFFFF;
  text-transform: uppercase;
  font-weight: 700;
  padding: 1rem;
}

.crud {
  width: 100%;
  max-width: 100rem;
}

/** Media Queries **/
.header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: #404040;
}
.header__h2 {
  font-weight: 700;
  color: #FFFFFF;
  font-size: 3rem;
  padding: 0 5rem;
}
@media (min-width: 1024px) {
  .header__h2 {
    text-align: start;
    font-size: 8rem;
    margin-right: 20vw;
  }
}
.header--homepage {
  background-image: url(../../../build/img/header.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  min-height: 50rem;
}
@media (min-width: 768px) {
  .header--homepage {
    min-height: 60;
  }
}
@media (min-width: 1024px) {
  .header--homepage {
    min-height: 100vh;
  }
}

/** Media Queries **/
.footer {
  padding: 5rem;
  background-color: #404040;
}
.footer .navegacion {
  display: flex;
  justify-content: start;
  font-size: 2rem;
}
.footer__p {
  color: #FFFFFF;
  text-align: center;
}

/** Media Queries **/
.descripcion {
  max-width: 120rem;
  padding: 0 2.5rem;
}
@media (min-width: 1024px) {
  .descripcion {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

.nosotros {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  max-width: 125rem;
}
.nosotros__cards {
  padding: 0 2.5rem;
}
@media (min-width: 768px) {
  .nosotros__cards {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.nosotros__img {
  height: 10rem;
}

/** Media Queries **/
.anuncios {
  display: grid;
  gap: 2rem;
  max-width: 120rem;
  padding: 0 2.5rem;
  padding-bottom: 2.5rem;
}
@media (min-width: 768px) {
  .anuncios {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .anuncios {
    grid-template-columns: repeat(3, 1fr);
  }
}
.anuncios__texto {
  padding: 2rem;
}
.anuncios__card {
  background-color: #eeeeee;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  list-style: none;
  margin: 0;
}
.anuncios__iconos {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 2rem;
  margin-bottom: 2rem;
}
.anuncios__link {
  background-color: #E08709;
  width: 100%;
  padding: 1rem 0;
  text-decoration: none;
  color: #FFFFFF;
}
.anuncios__link:hover {
  background-color: #c77808;
}
.anuncios h3,
.anuncios h4,
.anuncios p {
  margin: 0;
}
.anuncios__contenido {
  padding: 2rem;
  display: flex;
  flex-direction: column;
}

.iconos__card {
  display: flex;
  align-items: center;
  gap: 2rem;
}
.iconos__card img {
  height: 4rem;
  width: 4rem;
}

.txt-verde {
  color: #71B100;
  font-weight: 700;
}

/** Media Queries **/
.navegacion {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  padding: 0 5rem;
}
@media (min-width: 768px) {
  .navegacion__logos {
    display: flex;
    align-items: center;
    justify-content: center;
  }
}
@media (min-width: 768px) {
  .navegacion {
    flex-direction: row;
  }
}
.navegacion__h1 {
  color: #FFFFFF;
  text-transform: uppercase;
  font-size: 4rem;
}
@media (min-width: 768px) {
  .navegacion__h1 {
    font-size: 4rem;
  }
}
.navegacion__h1--span {
  font-weight: 700;
}
.navegacion__ul {
  list-style: none;
  text-decoration: none;
  padding-left: 0;
  font-size: 2rem;
  visibility: hidden;
  opacity: 0;
  height: 0;
  transition: display 0.3s ease-in-out, opacity 0.5s linear, height 0.5s linear;
}
@media (min-width: 768px) {
  .navegacion__ul {
    display: flex;
    font-size: 2rem;
    height: 100%;
    visibility: visible;
    opacity: 1;
  }
}
.navegacion__a {
  color: #FFFFFF;
  font-weight: 700;
}
@media (min-width: 768px) {
  .navegacion__a {
    margin-right: 1rem;
    font-weight: 300;
  }
}
.navegacion__a:hover {
  color: #71B100;
}
.navegacion__darkmode {
  filter: invert(100%);
  cursor: pointer;
}
.navegacion__barra {
  cursor: pointer;
}
@media (min-width: 768px) {
  .navegacion__barra {
    display: none;
  }
}

.aparecer {
  visibility: visible;
  opacity: 1;
  height: auto;
}

/** Media Queries **/
.blog {
  max-width: 80rem;
  padding: 0 2.5rem;
  padding: 1rem;
}
.blog__cards {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
@media (min-width: 768px) {
  .blog__card {
    display: grid;
    grid-template-columns: 1fr 2fr;
    gap: 2rem;
    align-items: center;
  }
}
.blog__link {
  text-decoration: none;
  color: #000000;
}
.blog__link h3 {
  font-size: 2.5rem;
  font-weight: 700;
  text-align: start;
  margin: 0;
}
.blog__link h3::after {
  content: "";
  display: block;
  width: 15rem;
  height: 0.5rem;
  background-color: #71b100;
  margin-top: 1rem;
}
.blog__span {
  color: #E08709;
}
.blog__texto {
  padding: 1rem;
}
.blog__entrada {
  max-height: 40rem;
}

/** Media Queries **/
.formulario {
  max-width: 120rem;
  padding: 0 2.5rem;
}
.formulario form {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
.formulario__formato {
  display: flex;
  flex-direction: column;
  border: 1px solid #000000;
  padding: 2rem;
  position: relative;
}
.formulario__formato input,
.formulario__formato textarea,
.formulario__formato select,
.formulario__formato option {
  border-radius: 1rem;
  padding: 1rem;
  margin-bottom: 1rem;
}
.formulario input[type=submit] {
  background-color: #71B100;
  border: none;
  display: flex;
  width: fit-content;
  padding: 1rem 2rem;
  font-weight: 700;
  margin-bottom: 2rem;
}

.mini {
  font-size: 1.2rem;
}

.error {
  background-color: rgb(202, 13, 13);
  text-align: center;
  color: #FFFFFF;
}

.contacto {
  display: flex;
  align-items: baseline;
  gap: 2rem;
}

.legend {
  position: absolute;
  top: -4rem;
  background-color: #FFFFFF;
}
.legend__darkmode {
  background-color: #1f1f1f;
}